import React from "react";
import "../First/First.css";
import ProgressBar from "react-bootstrap/ProgressBar";

export default function PreQuestion({ changeScreen, answer }) {
  const handleYes = () => {
    answer(true);
    return changeScreen(1);
  };

  const handleNo = () => {
    answer(false);
    return changeScreen(1);
  };

  return (
    <div className="menu-wrapper">
      <div className="menu-container">
        <div className="container">
          <div className="game-content">
            <div className="question-container">
              <p className="question-title">
                Você já produz conteúdo audiovisual em alguma plataforma?
              </p>
            </div>
            <div className="button-container">
              <button
                className="button yn-button"
                type="button"
                onClick={() => handleYes()}
              >
                <p style={{ marginTop: 8 }}>Sim</p>
              </button>
              <button
                className="button yn-button"
                type="button"
                onClick={() => handleNo()}
              >
                <p style={{ marginTop: 8 }}>Não</p>
              </button>
            </div>
            <div className="progress-bar">
              <ProgressBar
                animated
                variant="success"
                now={5}
                style={{ marginBottom: 20 }}
              />
            </div>
            <div className="player-button-container">
              <button type="button">
                <img
                  className="play-button"
                  alt="Botão de inicio da interação"
                  src="../../Assets/botao_voltar_proximo.svg"
                />
              </button>
              <button type="button">
                <img
                  className="play-button"
                  alt="Botão de inicio da interação"
                  src="../../Assets/botao_pause.svg"
                />
              </button>
              <button type="button">
                <img
                  className="play-button rotate"
                  alt="Botão de inicio da interação"
                  src="../../Assets/botao_voltar_proximo.svg"
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
