import React, { useState } from "react";
import "./First.css";
import ProgressBar from "react-bootstrap/ProgressBar";

export default function First({ previousAnswer }) {
  const [percentage, setPercentage] = useState(5);

  const handleYes = () => {
    setPercentage(percentage + 5);
  };

  return (
    <div className="menu-wrapper">
      <div className="menu-container">
        <div className="container">
          <div className="game-content">
            <div className="question-container">
              <p className="question-title">
                {previousAnswer
                  ? "Qual plataforma você utiliza?"
                  : "Você já sabe que tipo de conteúdo vai postar?"}
              </p>
            </div>
            <div className="button-container">
              <button
                className="button"
                type="button"
                onClick={() => handleYes()}
              >
                <img
                  src="https://www.techtube.com.br/wp-content/uploads/2019/01/como-baixar-snaptube-no-pc.jpg"
                  className="logo-social-media"
                />
                <p>Youtube</p>
              </button>
              <button
                className="button"
                type="button"
                onClick={() => handleYes()}
              >
                <img
                  src="https://upload.wikimedia.org/wikipedia/commons/5/51/Facebook_f_logo_%282019%29.svg"
                  className="logo-social-media"
                />
                <p>Facebook</p>
              </button>
              <button
                className="button"
                type="button"
                onClick={() => handleYes()}
              >
                <img
                  src="https://www.pngtube.com/myfile/detail/5-52080_twitch-logo-png-transparent-background-twitch-logo.png"
                  className="logo-social-media"
                />
                <p>Twitch TV</p>
              </button>
            </div>
            <div className="progress-bar">
              <ProgressBar
                animated
                variant="success"
                now={percentage}
                style={{ marginBottom: 20 }}
              />
            </div>
            <div className="player-button-container">
              <button onClick={() => null} type="button">
                <img
                  className="play-button"
                  alt="Botão de inicio da interação"
                  src="../../Assets/bg_pattern.gif"
                />
              </button>
              <button onClick={() => handleYes()} type="button">
                <img
                  className="play-button"
                  alt="Botão de inicio da interação"
                  src="../../Assets/botao_pause.svg"
                />
              </button>
              <button onClick={() => handleYes()} type="button">
                <img
                  className="play-button rotate"
                  alt="Botão de inicio da interação"
                  src="../../Assets/botao_voltar_proximo.svg"
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
