import React from "react";
import "./Menu.css";

export default function Menu({ startInfographic, setName, name }) {
  const handleYes = () => {
    return startInfographic(1);
  };

  return (
    <div className="menu-wrapper">
      <div className="menu-container">
        <div className="menu-content">
          <p style={{ textAlign: "center" }}>
            SAMUEL E OS SAMUELITOS APRESENTAM
          </p>
          <h2 className="title">Onde devo monetizar meus vídeos ?</h2>
          <p>
            Já ouviu falar em <b>monetização</b>? Derivado do verbo monetizar, o
            seu significado está ligado a transformação de qualquer coisa em
            fonte de renda. Seja um objeto, uma ideia, um site ou algo que está
            presente no dia a dia de tantas pessoas, a monetização permite criar
            ilimitadas oportunidades para ganhar dinheiro.
          </p>
          <p>
            Se você quer ser um <b>produtor</b> de conteúdo e não sabe qual
            plataforma é o melhor lugar para você? Quem sabe nós podemos te
            ajudar a decidir :)
            <br /> Insira seu nome abaixo, aperte o play e vem com a gente
            descobrir a melhor casa das suas produções audiovisuais.
          </p>
          <div className="name-container-wrapper">
            <div className="name-container">
              <input
                className="insert-name-input"
                value={name}
                onChange={e => setName(e.target.value)}
                type="input"
                placeholder="Digite seu nome"
              />
              <button onClick={() => handleYes()} type="button">
                <img
                  className="play-button"
                  alt="Botão de inicio da interação"
                  src="http://underway.ie/cms/sites/default/files/styles/large/public/field/image/play%20button.png?itok=ijy_9s1v"
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
