import React from "react";
import "./App.css";

import Menu from "./Pages/Menu/Menu";
import PreQuestion from "./Pages/PreQuestion/PreQuestion";
import First from "./Pages/First/First";

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeComponent: 0,
      userName: "",
      answers: []
    };
  }

  handleActiveScreen = () => {
    const { activeComponent, userName, answers } = this.state;

    switch (activeComponent) {
      default:
        return;
      case 0:
        return (
          <Menu
            startInfographic={this.next}
            setName={this.setName}
            name={userName}
          />
        );
      case 1:
        return <PreQuestion changeScreen={this.next} answer={this.addAnswer} />;
      case 2:
        return <First previousAnswer={answers[0]} />;
    }
  };

  setName = value => {
    this.setState({ userName: value });
  };

  addAnswer = value => {
    this.setState(prevState => ({ answers: [...prevState.answers, value] }));
  };

  next = value => {
    this.setState(prevState => ({
      activeComponent: prevState.activeComponent + value
    }));
  };

  render() {
    return <>{this.handleActiveScreen()}</>;
  }
}
